.content {
  color: whitesmoke;
  background-image: -webkit-radial-gradient(#0F509C, #0d478c) !important;
  background-image: radial-gradient(#0F509C, #0d478c) !important;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 70%;
  margin: 0px !important;
}

.company-logo {
  position: absolute !important;
  margin-left: auto !important;
  margin-right: auto !important;
  left: 0;
  right: 0;
  bottom: 20px;
}

.footer {
  padding: 30px 0px !important;
  margin: 0 !important;
  height: 30%;
}
.navbar-menu {
  -webkit-flex: 0 0 auto;
          flex: 0 0 auto;
  border-bottom: 1px solid #0e4f9a !important;
}
div.ui.icon.input > input {
  width: 100em;
}

.ui.category.search .results {
  width: 100em;
}
